@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: 'Mulish', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar{
  background-color: transparent(#ffffff00) !important;
}

.navbar-brand{
  font-size: 1.5rem !important;
  font-weight: 600;
  color: #565387 !important;
  transition: .3s;
}

.navbar-brand:hover{
  color: #3498db !important;
}

.nav-item a{
  font-size: 1.0rem;
  font-weight: 500 !important;
  text-transform: uppercase;
  transition: .3s;
}

.nav-item a:hover{
  color: #3498db !important;
}

.navbar-nav{
  margin-left: auto !important; 
}

.nav-item{
  padding: 0px 20px;
}

.activeMenuClass{
  font-weight: bold;
  border-bottom: 1px solid #565387;
  color: #3498db !important;
}

.text-uppercase{
  text-transform: uppercase !important;
}



#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

.content-holder{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 550px;
}

.content-holder h1{
  margin: 0 0 10px 0;
  font-size: 35px;
  font-weight: 700;
  color: #000000;
  width:auto;
  padding: 0 4px;
}

.BrandName{
  color: #3498db;
  font-weight: bold;
}

.content-holder h2{
  color: #484848;
  font-size: 18px;
  padding: 1px 0 5px 0;

}

.content-holder img{
  width: 100%;
}

.btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px ;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-transform: uppercase;
  text-decoration: none;
}

.half-width{
  width: 49%;
  padding:0  1%;
  float: left;
}


.btn-get-started:hover{
  background: #3498db;
  color: #fff;
}

.header-img{
  text-align: right;
}

.content-holder .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}


@keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}



@media only screen and (max-width: 630px) {
  .half-width{
    width: 99%;
    float: none;
  }
  
}








*{
    margin: 0;
    padding: 0;
}

body{
    background: #F5F8FD !important;
    font-family: 'Open Sans', sans-serif;
    overflow-x: hidden;
    width: 100vw;
}

.service-header{
    width: 100%;
    text-align: center;
    margin: 50px 0;
}

.service-header h1{
    font-size: 36px;
    font-weight: 700;
    color: #413e66;
    font-family: 'Montserrat', sans-serif;
}

.service-header p{
    font-size: 15px;
    font-weight: 400;
    width: 560px;
    margin: auto;
    color: #535075;
    padding: 10px 0;
}

.Icon{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 35px;
    width: 60px;
    height: 60px;
    border-radius: 100%;
}

#GlobeIcon{
    color: #ff689b;
    background: #FCEEF3;
}

#MobileIcon{
    color: #e98e06;
    background: #FFF0DA;
}

#LaptopIcon{
    color: #3fcdc7;
    background: #E6FDFC;
}

#WordpressIcon{
    color: #41cf2e;
    background: #EAFDE7;
}

#BulbIcon{
    color: #2282ff;
    background: #E1EEFF;
}

#TimerIcon{
    color: #8660fe;
    background: #ECEBFF;
}

.service-menu{
    text-align: center;
}

.SingleServiceMenu{
    width: 360px;
    height: 350px;
    display: inline-block;
}

.box{
    width: 340px;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 45px;
    border-radius: 10px;
    box-shadow: 0 0 30px rgba(51, 51, 51, 0.187);
    transition: 0.4s;
    color: #333;
    cursor: pointer;
}

.box:hover{
    transform: scale(1.1);
    transition: 0.4s;
}

.box:hover h2{
    color: #1BB1DC;
    font-size: 22px;
}


.SingleServiceMenu h2{
    font-size: 18px;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    padding-top: 15px;
    transition: 0.3s;
}


.SingleServiceMenu p{
    font-size: 14px;
    font-weight: 400;
    text-align: left;
}


@media only screen and (max-width: 630px) {
    body{
        width: 100vw;
    }
    .service-header p{
        font-size: 15px;
        font-weight: 400;
        width: auto;
        margin: auto;
        color: #535075;
        padding: 10px 0;
    }

}

@media only screen and (max-width: 390px) {
    .SingleServiceMenu{
        transform: translate(-20px);
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.Contact-Head{
  font-size: 36px;
  font-weight: 700;
  color: #413e66;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  padding-top: 25px;
}

.Contact-Text{
  font-size: 15px;
  font-weight: 400;
  width: 560px;
  margin: auto;
  color: #535075;
  padding: 10px 0;
  text-align: center;
  padding-bottom: 30px;
}

.purple-btn{
  background: #535075;
  color: #ffffff;
}

.purple-btn:hover{
  background: #413e66;
  color: #ffffff;
}


